@import url(http://fonts.googleapis.com/css?family=Lato:400,700);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "lato", sans-serif;
}
.home,
.vechiles,
.workers,
.schedule,
.incomeAndOutcome {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.map {
  display: flex;
  height: 90vh;
  padding-left: 500px;
  padding-top: 77px;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.navbar {
  background-color: #060b26;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background-color: none;
}
.nav-menu {
  background-color: #060b26;
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}
.nav-menu.active {
  left: 0;
  transition: 350ms;
}
.nav-text {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}
.nav-text a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}
.nav-text a:hover {
  background-color: #1a83ff;
}
.nav-menu-items {
  width: 100%;
}
.navbar-toggle {
  background-color: #060b26;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
span {
  margin-left: 16px;
}

.infoo {
  width: 500px;
  margin-left: 750px;
  padding-left: 10px;
  border: 3px solid rgb(226, 226, 226);
  float: center;
  margin-top: 0px;
  border-radius: 25px;
  box-shadow: 10cm;
}
.container-picture {
  border: 3px solid #212529;
  width: 290;
  margin-left: 300px;
  margin-top: 30px;
  padding-left: 20px;
  size: 24px;
  border-radius: 25px;
  float: left;
}
.change-bu {
  position: fixed;
  margin-top: 320px;
  margin-left: 350px;
}
.picture {
  padding-right: 10px;
  padding-top: 7px;
  padding-bottom: 7px;
}

#schedule-build {
  background-color: #edf5e1;
  color: #05386b;
  padding: 0px 20px 10px 20px;
}
.fullName {
  font-size: 16px;
}

.browser-default {
  border-radius: 5px;
}

.btn-color {
  background-color: #0e1c36;
  color: #fff;
}

.profile-image-pic {
  height: 200px;
  width: 200px;
  object-fit: cover;
}

.cardbody-color {
  background-color: #ebf2fa;
}

a {
  text-decoration: none;
}

